* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    background-color: #192655;
}

#root {
    position: relative;
}

/* * ========== Style The Header: =========== */
header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2px;
    position: fixed;
    top: 0;
    z-index: 100;
    box-shadow: 1px 1px 1px 1px gray;
    background-color: #192655;
    color: white;
}

.toggleBackground {
    background-color: #0ab3b3;
    color: black;
    transition: .5s;
}

header img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-shadow: 1px 1px 1px 1px #fff;
    cursor: pointer;
    margin-left: 50px;
}

header img:hover {
    animation: logo-rotate 1.2s forwards ease-in-out 1;
    opacity: 0.9;
}

@keyframes logo-rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

header p {
    font-size: 1.5em;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin-right: 50px;
    text-shadow: 1px 1px 1px #fff;
}

/* *======== Style the input search: =========*/

.container .input-box {
    margin-top: 100px;
    margin-inline: auto;
    width: fit-content;
    background-color: #fff;
    padding: 2px 4px 2px 10px;
    border-radius: 2px;
    display: grid;
    row-gap: 5px;
    grid-template-columns: auto auto;
    align-items: center;
    justify-content: space-between;
    column-gap: 5px;
    position: relative;
}

.input-box input {
    padding: 7px 7px 7px 10px;
    outline: none;
    border: none;
    font-family: "Agbalumo";
    font-size: 1.1em;
    letter-spacing: 1px;
    font-weight: bold;
    text-transform: capitalize;
}

.input-box .btnSearch {
    border: 1px solid gray;
    padding: 10px 15px;
    border-radius: 2px;
    background-color: #192655;
    box-shadow: 1px 1px 1px 1px #000;
    transition: all .7s;
    cursor: pointer;

}

.input-box .btnSearch i {
    color: #fff;
    text-shadow: 1px 1px 1px black;
}

.input-box .btnSearch:hover {
    text-shadow: none;
    box-shadow: 1px 1px 1px 2.5px #000 inset;
    opacity: 0.9;
}

.input-box .warningMessage {
    position: absolute;
    top: -22px;
    left: 10px;
    font-size: 0.6em;
    color: orange;
    font-weight: bold;
    font-family: sans-serif;
    transform: scale(0);
    transition: .6s;
}

.input-box .warningMessage:before {
    content: "";
    position: absolute;
    width: 5px;
    height: 5px;
    background-color: orange;
    border-radius: 50%;
    top: 50%;
    transform: translateY(-50%);
    left: -10px;
    border: 1px solid white;
}

/* *===== Style the meanings word container ====*/

.meaningsContainer {
    width: 100%;
    margin-top: 20px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 15px;
    padding: 10px;
}

/** ====== Style the word meaning box ======= */

.meaningsContainer .wordMeanings {
    width: 450px;
    background-color: #FFCD4B;
    padding: 10px;
    border-radius: 10px;
    align-self: stretch;
    position: relative;
    overflow: hidden;
}

.meaningsContainer .wordMeanings::before,
.meaningsContainer .wordMeanings::after {
    content: "";
    width: 25px;
    height: 25px;
    top: 0;
    position: absolute;
    background-color: #E9B824;

}

.meaningsContainer .wordMeanings::before {
    left: 0;
    border-bottom-right-radius: 10px;
}

.meaningsContainer .wordMeanings::after {
    right: 0;
    border-bottom-left-radius: 10px;
}


/** ========== Style the word ========= */
.wordMeanings .word-info .theWord {
    font-family: "Agbalumo";
    font-size: 2em;
    color: red;
    text-align: center;
    text-transform: capitalize;
}

/** ========== style the phonetic ========== */

.phon-partS-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    column-gap: 10px;
    margin-block: 5px;
    position: relative;
    margin-left: 30px;
}

.phon-partS-box::before {
    content: "";
    width: 8px;
    height: 8px;
    position: absolute;
    background-color: red;
    transform: rotate(45deg) translateY(-50%);
    left: -20px;
    top: 50%;
}

.wordMeanings .word-info .phonetic {
    font-family: "Agbalumo";
    font-size: 0.9em;
    color: green;
}

/** ========== style the word type ============= */
.wordMeanings .word-info .partOfSpeech {
    font-family: "Agbalumo";
    font-size: 1em;
    color: blue;
    text-transform: capitalize;
}

/* *================== */


/**======== style word pronunciation: ========= */
.wordMeanings .word-info .audios-box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: relative;
    margin-bottom: 25px;
}

.wordMeanings .word-info .audios-box::after {
    content: "";
    position: absolute;
    width: 90%;
    height: 5px;
    background-color: rgba(0, 0, 0, 0.3);
    bottom: -10px;
    border-radius: 100px;
}

.wordMeanings .word-info .audios-box audio {
    width: 120px;
    cursor: pointer;
}

.wordMeanings .word-info .audios-box .pron-country {
    text-align: center;
    font-size: 0.6em;
    font-weight: 600;
    font-family: sans-serif;
}

/**====style the word synonyms and antonyms :==== */

.synonyms-box {
    width: 90%;
    margin: auto;
}

.synonyms-box .synonyms-title,
.antonyms-box .antonyms-title {
    font-size: 0.7em;
    font-family: sans-serif;
    font-weight: 600;
    margin-bottom: 2px;
    position: relative;
    margin-left: 35px;
}

.synonyms-box .synonyms-title::before,
.antonyms-box .antonyms-title::before {
    content: "";
    width: 8px;
    height: 8px;
    background-color: red;
    position: absolute;
    left: -20px;
    top: 50%;
    transform: rotate(45deg) translateY(-50%);
}

.word-info .synonyms,
.word-info .antonyms {
    margin-inline: auto;
    width: 95%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    margin-bottom: 20px;
    padding: 5px 10px;
    position: relative;
    border: 2px solid #fff;
    background-color: #E9B824;
}

.word-info .synonyms p,
.word-info .antonyms p {
    text-transform: capitalize;
    margin-inline: 10px;
    font-weight: 1.1em;
    line-break: strict;
    font-family: sans-serif;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    word-break: keep-all;
}

.word-info .antonyms {
    background-color: #F0DE36;
}

/**======== style the definitions:=========== */

.wordMeanings .definitions {
    margin: auto;
    width: 90%;
    font-family: "Manrope";
    font-size: 0.8em;
}

.wordMeanings .definitions .definition-title {
    font-weight: bold;
    position: relative;
    margin-bottom: 10px;
    width: fit-content;
}

.wordMeanings .definitions .definition-title::before {
    content: "";
    width: 100%;
    height: 2px;
    background-image: linear-gradient(to right, #75C2F6, #F11A7B, white);
    left: 0;
    bottom: 0;
    position: absolute;
    border-radius: 10px;
}

.wordMeanings .definitions p:not(.definition-title) {
    color: black;
    margin-block: 5px;
    position: relative;
    text-indent: 15px;
}

.wordMeanings .definitions p:not(.definition-title)::before {
    content: "";
    width: 8px;
    height: 8px;
    background-image: linear-gradient(to left, #F11A7B, #75C2F6, white);
    position: absolute;
    top: 10px;
    left: -5px;
    border-radius: 50%;
}

.wordMeanings .examples {
    margin-top: 35px;
    margin-inline: auto;
    width: 95%;
    font-family: "Agbalumo";
    font-size: 15px;
    background-color: orange;
    padding: 10px;
    border-radius: 5px;
    position: relative;
}

.wordMeanings .examples::before {
    content: "Example:";
    position: absolute;
    top: -27px;
    left: 0;
    color: #fff;
    color: #0D1282;
    text-shadow: 1px 1px 1px white;
    font-size: 1.2em;
}

/* *========== Style the error =================*/
.error-box {
    color: white;
    width: 60%;
    background-color: red;
    padding: 20px;
    border-radius: 20px;
    border-radius: 10px;
    margin: 20px auto;
}

/* *============ Style the footer ==========*/

footer {
    width: 100%;
    background-color: white;
    padding: 10px;
    margin-top: 50px;
    font-family: sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
}

footer p {
    font-size: 0.8em;
}

footer p>span {
    color: red;
    font-weight: 600;
    text-decoration: underline;
}

footer p a {
    background-color: rgba(0, 0, 0, 0.1);
    padding: 2px 10px;
    border-radius: 10px;
    text-decoration: none;
    box-shadow: 1px 1px 1px 1px gray;
    transition: .7s;
}

footer p a:hover {
    box-shadow: none;
}

/* *========== Style go back button: =============*/

.backTopBtn {
    background-color: #0ab3b3;
    position: fixed;
    bottom: 5px;
    right: 1.5px;
    cursor: pointer;
    border: none;
    outline: none;
    padding: 1px;
    border-radius: 5px;
    transform: scale(0);
    transition: .5s;
}

.backTopBtn i {
    color: white;
    font-size: 1.7em;
}

@media (max-width: 768px) {
    * {
        font-size: 20px;
    }

    .meaningsContainer .wordMeanings {
        width: 90%;
    }

    footer {
        flex-direction: column;
        font-size: 1.3em;
    }
}